import * as React from 'react';
import { LineChart, lineElementClasses } from '@mui/x-charts/LineChart';

const uData = [4000, 3000, 1500, 2780, 4972, 2390, 3490, 2005, 3500, 5499, 5716, 6594];
const xLabels = [
  'Jan',
  'Feb',
  'Mar',
  'Apr',
  'May',
  'Jun',
  'Jul',
  'Aug',
  'Sep',
  'Oct',
  'Nov',
  'Dec'
];

export default function StatisticsAreaChart() {
  return (
    <LineChart
      width={1000}
      height={600}
      series={[
        {
          data: uData,
          label: 'Total Amount ($)',
          area: true,
          showMark: false,
          color: 'FB1465',
        },
      ]}
      xAxis={[{ scaleType: 'point', data: xLabels }]}
      sx={{
        [`& .${lineElementClasses.root}`]: {
          display: 'none',
        },
      }}
    />
  );
}
